.hero-container {
  display: flex;
  align-items: center;
  flex-direction: column;

  @media screen and (min-width: 768px) {
    align-items: flex-start;
    padding-bottom: 80px;
  }

  @media screen and (min-width: 1280px) {
    padding-bottom: 56px;
  }
}
.hero__tittle {
  color: var(--primary-text-color);
  font-family: Caveat Brush;
  font-size: 30px;
  line-height: 38px;
  align-items: center;
  vertical-align: top;
  text-align: center;
  margin-top: 33px;
  z-index: 100;

  @media screen and (min-width: 768px) {
    margin-top: 10px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 64px;
    line-height: 65px;
    margin-top: 57px;
  }
}

.hero__label {
  margin-top: 3px;
  margin-bottom: 20px;
  text-align: center;

  @media screen and (min-width: 1280px) {
    font-size: 24px;
    line-height: 38px;
    margin-top: 15px;
    margin-bottom: 10px;
  }
}

.hero__button {
  min-width: 145px;
  height: 40px;
  background-color: var(--read-more-color);
  border-radius: 25px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  color: var(--background-color);
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 5;

  &:hover,
  &:focus,
  &:active {
    background-color: var(--focus-hover-color);
  }

  @media screen and(min-width: 768px) {
    margin-bottom: 15px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 25px;
  }
}
